import React from "react";
import PropTypes from "prop-types";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";

function DeleteConfirmation({ visible, onOk, onCancel, message }) {
  return (
    <Modal show={visible} centered size="lg" onHide={onCancel}>
      <Modal.Body>
        <div className="pt-1">{message}</div>
        <div className="pt-3 text-right">
          <Button variant="danger" className="ml-2" onClick={onOk}>
            Yes
          </Button>
          <Button variant="light" className="ml-2" onClick={onCancel}>
            No
          </Button>
        </div>
      </Modal.Body>
    </Modal>
  );
}

DeleteConfirmation.propTypes = {
  visible: PropTypes.bool,
  onCancel: PropTypes.func,
  onOk: PropTypes.func,
};

export default DeleteConfirmation;
