import React, { useState } from "react";
import PropTypes from "prop-types";
import Card from "react-bootstrap/Card";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Editor } from "@tinymce/tinymce-react";
import {
  faBook,
  faSitemap,
  faSave,
  faPlus,
  faFileCode,
  faListUl,
  faQuoteLeft,
  faEye,
  faFile,
} from "@fortawesome/free-solid-svg-icons";
import { uploadImageFromBlobInfo } from "app/utils";

function LessonForm({ tinyMceApiKey, mode, lesson, onAdd, onUpdate, modules }) {
  const [data, setData] = useState(
    mode === "add"
      ? {
          title: "",
          slug: "",
          excerpt: "",
          content: "",
          isPreviewEnabled: false,
          lessonOrder: 0,
        }
      : { ...lesson }
  );

  const handleChange = (field, e) => {
    e.stopPropagation();
    if (field) {
      const newValue = e.target.value;
      const oldValue = data[field];
      if (newValue !== oldValue) {
        setData({
          ...data,
          [field]: newValue,
        });
      }
    }
  };

  const handlePreviewChange = (field, e) => {
    e.stopPropagation();
    if (field) {
      const newValue = e.target.checked;
      const oldValue = data[field];
      if (newValue !== oldValue) {
        setData({
          ...data,
          [field]: newValue,
        });
      }
    }
  };
  const handleEditorChange = (field, content) => {
    setData({
      ...data,
      [field]: content,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (mode === "add") onAdd(data);
    if (mode === "update") onUpdate(data);
  };

  const renderForm = () => {
    const {
      title,
      slug,
      excerpt,
      content,
      isPreviewEnabled,
      lessonOrder,
      sectionId,
    } = data;
    return (
      <Form onSubmit={handleSubmit}>
        <Card className="border-0">
          <Card.Body>
            <div className="text-muted">
              <div className="border-bottom mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faBook} className="mr-2" />
                  Title
                </strong>
                <Form.Group controlId="title" className="mb-0 mt-2">
                  <Form.Control
                    type="text"
                    value={title || ""}
                    placeholder="Enter title"
                    onChange={(e) => handleChange("title", e)}
                    required
                  />
                </Form.Group>
              </div>
            </div>
            <div className="text-muted pt-3">
              <div className="border-bottom mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faFile} className="mr-2" />
                  Module
                </strong>

                <Form.Group controlId="sectionId" className="mb-0 mt-2">
                  <Form.Control
                    as="select"
                    value={sectionId}
                    onChange={(e) => handleChange("sectionId", e)}
                    custom
                  >
                    {modules.map((mod) => (
                      <option key={mod.id} value={mod.id}>
                        {mod.name}
                      </option>
                    ))}
                  </Form.Control>
                </Form.Group>
              </div>
            </div>
            <div className="text-muted pt-3">
              <div className="border-bottom mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faListUl} className="mr-2" />
                  Lesson Order
                </strong>
                <Form.Group controlId="lessonOrder" className="mb-0 mt-2">
                  <Form.Control
                    type="number"
                    value={lessonOrder || ""}
                    placeholder="Enter order"
                    onChange={(e) => handleChange("lessonOrder", e)}
                    required
                  />
                </Form.Group>
              </div>
            </div>
            <div className="text-muted pt-3">
              <div className="border-bottom mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faSitemap} className="mr-2" />
                  Slug
                </strong>
                <Form.Group controlId="slug" className="mb-0 mt-2">
                  <Form.Control
                    type="text"
                    value={slug || ""}
                    placeholder="Enter slug"
                    onChange={(e) => handleChange("slug", e)}
                    required
                  />
                </Form.Group>
              </div>
            </div>
            <div className="text-muted pt-3">
              <div className="border-bottom mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faQuoteLeft} className="mr-2" />
                  Excerpt
                </strong>
                <Form.Group controlId="excerpt" className="mb-0 mt-2">
                  <Form.Control
                    type="text"
                    value={excerpt || ""}
                    placeholder="Enter excerpt"
                    onChange={(e) => handleChange("excerpt", e)}
                  />
                </Form.Group>
              </div>
            </div>
            <div className="text-muted pt-3">
              <div className="border-bottom mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faEye} className="mr-2" />
                  Enable Preview
                </strong>
                <Form.Group
                  controlId="isPreviewEnabled"
                  className="mb-0 mt-2 text-left"
                >
                  <label className="switch">
                    <input
                      type="checkbox"
                      checked={isPreviewEnabled}
                      onChange={(e) =>
                        handlePreviewChange("isPreviewEnabled", e)
                      }
                    />
                    <span className="slider round"></span>
                  </label>
                </Form.Group>
              </div>
            </div>
            <div className="text-muted pt-3">
              <div className="mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faFileCode} className="mr-2" />
                  Content
                </strong>
                <Form.Group controlId="content" className="mb-0 mt-2">
                  <Editor
                    apiKey={tinyMceApiKey}
                    key={"form-wvgiwys"}
                    initialValue={content || ""}
                    init={{
                      selector: "textarea",
                      images_upload_handler: function (
                        blobInfo,
                        success,
                        failure
                      ) {
                        uploadImageFromBlobInfo(blobInfo, success, failure);
                      },
                      height: 500,
                      menubar: false,
                      plugins: [
                        "advlist codesample autolink lists link image imagetools charmap print preview anchor",
                        "searchreplace visualblocks code fullscreen",
                        "insertdatetime media table paste code help wordcount",
                      ],
                      toolbar: `undo redo | formatselect | image | codesample | bold italic backcolor | 
             alignleft aligncenter alignright alignjustify | 
             bullist numlist outdent indent | removeformat | help`,
                      entity_encoding: "raw",
                    }}
                    onEditorChange={(content) =>
                      handleEditorChange("content", content)
                    }
                  />
                </Form.Group>
              </div>
            </div>
            <div className="pt-3">
              {mode === "add" && (
                <Button type="submit" variant="success">
                  <FontAwesomeIcon icon={faPlus} className="mr-2" />
                  Add Lesson
                </Button>
              )}
              {mode === "update" && (
                <Button type="submit" variant="success">
                  <FontAwesomeIcon icon={faSave} className="mr-2" />
                  Save Lesson
                </Button>
              )}
            </div>
          </Card.Body>
        </Card>
      </Form>
    );
  };

  return <div className="lesson-form">{renderForm()}</div>;
}

LessonForm.propTypes = {
  mode: PropTypes.string,
  lesson: PropTypes.object,
  onAdd: PropTypes.func,
};

export default LessonForm;
